// ------------------------------------------------------------------------
// -- BREAKPOINTS
// ------------------------------------------------------------------------

$tiny        : 480px !default;
$small       : 576px !default;
$medium      : 768px !default;
$large       : 992px !default;
$extra-large : 1200px !default;



// ------------------------------------------------------------------------
// -- FONTS / TYPO
// ------------------------------------------------------------------------

$font-family-base      : "EB Garamond", serif !default;
$font-family-headings  : "EB Garamond", serif !default;
$font-family-icons     : "icomoon" !default;

$font-size-base : 1.8rem !default;
$font-sizes: (
  base: (
    mobile  : 1.6rem,
    desktop : $font-size-base
  ),
  h1: (
    mobile  : 2.8rem,
    desktop : 3.2rem
  ),
  h2: (
    mobile  : 2.4rem,
    desktop : 2.8rem
  ),
  h3: (
    mobile  : 2.0rem,
    desktop : 2.4rem
  ),
  h4: (
    mobile  : 1.8rem,
    desktop : 2.0rem
  ),
  h5: (
    mobile  : 1.6rem,
    desktop : 1.8rem
  ),
  h6: (
    mobile  : 1.4rem,
    desktop : 1.6rem
  )
) !default;

$line-height-base  : 1.4 !default;
$line-heights: (
  base: (
    mobile  : 1.4,
    desktop : $line-height-base
  ),
  title: (
    mobile  : 1.2,
    desktop : 1.2
  )
) !default;

$margin-bottom-base      : 1rem !default;
$margin-top-headings  : $margin-bottom-base *2 !default;
$margin-bottom-headings  : $margin-bottom-base /2 !default;
$margin-bottom-paragraph : 2rem !default;



// ------------------------------------------------------------------------
// -- COLORS
// ------------------------------------------------------------------------

$white    : #fff !default;
$gray-100 : #e5e5e5 !default;
$gray-200 : #ccc !default;
$gray-300 : #b3b3b3 !default;
$gray-400 : #999 !default;
$gray-500 : #7f7f7f !default;
$gray-600 : #666 !default;
$gray-700 : #4d4d4d !default;
$gray-800 : #333 !default;
$gray-900 : #1a1a1a !default;
$black    : #000 !default;

$color-base        : #ffffff !default;
$bg-base           : #000000 !default;

$color-primary     : #d5ae5f !default;
$color-secondary   : #000000 !default;
$color-tertiary    : #8b512c !default;

$color-success     : #5CB85C !default;
$color-warning     : #F0AD4E !default;
$color-danger      : #D9534F !default;
$color-info        : #5BC0DE !default;

$link-color            : $color-primary;
$link-color-hover      : $color-secondary;



// ------------------------------------------------------------------------
// -- LAYOUT
// ------------------------------------------------------------------------

$wrapsize: 1120px;



// ------------------------------------------------------------------------
// -- MIXINS
// ------------------------------------------------------------------------

$bp-aliases: (
    'tiny'          : (max-width: #{$tiny - 1}),
    'small'         : (max-width: #{$small - 1}),
    'medium'        : (max-width: #{$medium - 1}),
    'large'         : (max-width: #{$large - 1}),
    'extra-large'   : (max-width: #{$extra-large - 1}),
    'tiny-up'       : (min-width: #{$tiny}),
    'small-up'      : (min-width: #{$small}),
    'medium-up'     : (min-width: #{$medium}),
    'large-up'      : (min-width: #{$large}),
    'extra-large-up': (min-width: #{$extra-large}),
    'retina'        : (min-resolution: 2dppx)
);

@mixin respond-to($name) {
    @if map-has-key($bp-aliases, $name) {
        @media #{inspect(map-get($bp-aliases, $name))} {
            @content;
        }
    }
    @else {
        @warn "Unfortunately, no value could be retrieved from `#{$breakpoint}`. "
        + "Please make sure it is defined in `$breakpoints` map.";
    }
}

@mixin font-size($elem) {
    $q: map-get($font-sizes, $elem);
    $mob: map-get($q, "mobile");
    $desk: map-get($q, "desktop");
    font-size: $mob;
    @include respond-to("small-up") {
        font-size: $desk;
    }
}

@mixin line-height($elem) {
    $q: map-get($line-heights, $elem);
    $mob: map-get($q, "mobile");
    $desk: map-get($q, "desktop");
    line-height: $mob;
    @include respond-to("small-up") {
        line-height: $desk;
    }
}