/* -------------------------------------------------------------------------*/
/* -- FONTS */
/* -------------------------------------------------------------------------*/

@font-face {
    font-family: "EB Garamond";
    font-weight: bold;
    font-style: normal;
    src: url("../fonts/ebgaramond/EBGaramond-Bold.woff2") format("woff2"),
    url("../fonts/ebgaramond/EBGaramond-Bold.woff") format("woff");
}

@font-face {
    font-family: "EB Garamond";
    font-weight: normal;
    font-style: normal;
    src: url("../fonts/ebgaramond/EBGaramond-Regular.woff2") format("woff2"),
    url("../fonts/ebgaramond/EBGaramond-Regular.woff") format("woff");
}



/* -------------------------------------------------------------------------*/
/* -- GLOBAL */
/* -------------------------------------------------------------------------*/

.main {
    padding-top: 5rem;
}

.main__section {
    & + .main__section {
        margin-top: 5rem;
    }
}

.main__content {
    font-size: 1.8rem;
    text-align: justify;
}
.main__content_center {
    text-align: center;
}

.main__ttl {
    margin-bottom: 2rem;
}
.main__ttl_center {
    text-align: center;
}

.cookiebanner {
    display: none;
    position: fixed;
    z-index: 50;
    left: 0;
    bottom: 0;
    right: 0;
    background-color: $gray-900;
    text-align: center;
    padding: 1.5rem;
}
.cookiebanner_active {
    display: block;
}
.cookiebanner__btn {
    margin-top: 1rem;
}

.sep {
    margin: 5rem 0;
    text-align: center;
}

.has-lines {
    display: flex;
    justify-content: center;
    align-items: center;
    text-align: center;
    hyphens: none;
    &:before,
    &:after {
        content: "";
        flex-grow: 1;
        height: 1px;
        opacity: .2;
        margin-top: .2em;
        background-color: currentColor;
    }
    &:before {
        margin-right: .5em;
    }
    &:after {
        margin-left: .5em;
    }
}
.link_insta {
    text-align: center;
}
.link_insta a:hover {
    text-align: center;
    color: #fff;
}

/* -------------------------------------------------------------------------*/
/* -- LAYOUT */
/* -------------------------------------------------------------------------*/

.layout__col {
    & + .layout__col {
        margin-top: 1.5rem;
    }
}


/* -------------------------------------------------------------------------*/
/* -- BTN */
/* -------------------------------------------------------------------------*/

.btn {
    display: block;
    padding: .5rem 2rem;
    font-size: 1.4rem;
    text-align: center;
    text-transform: uppercase;
    transition: all .3s ease;
    [class*="icon"] {
        margin-left: 1.5rem;
    }
}

.btn_giant {
    padding-top: 1rem;
    padding-bottom: 1rem;
    font-size: 2rem;
    font-weight: bold;
}

.btn_neutre {
    border: 1px solid $color-tertiary;
    color: $bg-base;
    &:focus,
    &:hover,
    &:active {
        background-color: $color-primary;
    }
}

.btn_primary {
    background-color: $color-primary;
    color: $bg-base;
    &:focus,
    &:hover,
    &:active {
        background-color: $color-tertiary;
        color: $white;
    }
}



/* -------------------------------------------------------------------------*/
/* -- FORMS */
/* -------------------------------------------------------------------------*/

.form__label {
    display: block;
    font-size: 1.6rem;
}

.form__input {
    display: block;
    width: 100%;
    background-color: lighten($gray-100,8);
    border: 1px solid $gray-100;
    border-radius: 3px;
    color: $bg-base;
    font-size: 1.6rem;
    transition: border-color .3s ease;
    &_error {
        border-color: $color-tertiary;
    }
    &:focus {
        border-color: $color-primary;
    }
}

.form__grp {
    margin-bottom: 1rem;
}

.form__msg {
    margin-bottom: 2rem;
    font-size: 1.4rem;
    color: $color-tertiary;
}



/* -------------------------------------------------------------------------*/
/* -- HEADER */
/* -------------------------------------------------------------------------*/

.header__logo {
    display: block;
    max-width: 280px;
    margin: 0 auto;
    padding: 2rem;
    text-align: center;
}



/* -------------------------------------------------------------------------*/
/* -- TELEPHONE */
/* -------------------------------------------------------------------------*/

.bloctel {
    padding: .75rem 1rem;
    text-align: center;
}
.bloctel_primary {
    background-color: lighten($bg-base,13);
    color: $color-primary;
}

.bloctel__icon {
    margin-right: 1rem;
}

.bloctel__num {
    font-weight: bold;
}



/* -------------------------------------------------------------------------*/
/* -- NAV */
/* -------------------------------------------------------------------------*/

.nav__inner {
    display: none;
    margin: 0;
    padding: 0;
    list-style: none;
    &_open {
        display: block;
    }
}

.nav__link {
    display: inline-block;
    width: 100%;
    padding: 1rem;
    border-top: 1px solid $bg-base;
    font-size: 1.4rem;
    text-align: center;
    text-transform: uppercase;
}

.nav__mobile {
    display: flex;
    .nav__link {
        flex: 1 1 0%;
        border-top: 0;
        font-size: 2rem;
        & + .nav__link {
            border-left: 1px solid $bg-base;
        }
    }
}

.nav__sub {
    margin: 0;
    padding: 0;
    list-style: none;
}

.nav_primary {
    background-color: $color-primary;

    .nav__link {
        color: $bg-base;
        &:focus,
        &:hover,
        &:active,
        &.nav__link_current {
            color: $color-tertiary;
        }
    }

    .nav__item_has-sub {
        &:focus,
        &:hover,
        &:active {
            & > .nav__link {
                color: $color-tertiary;
            }
        }
    }
}



/* -------------------------------------------------------------------------*/
/* -- BLOC IMG */
/* -------------------------------------------------------------------------*/

.imgblock_dual,
.imgblock_dual-inv {
    display: flex;
    flex-direction: column;
    .imgblock__content {
        position: relative;
        z-index: 1;
        order: -1;
        width: 90%;
        margin: 0 auto -30% auto;
    }
}
.encard {
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    gap: 10px;

    .imgblock_real_professionnels,
    .imgblock_real_particuliers {
        position: relative;

        .imgblock__content {
            position: absolute;
            top: 50%;
            right: 0;
            left: 0;
            width: 90%;
            margin: 0 auto;
            transform: translateY(-50%);

            .txtblock__ttl {
                margin-bottom: 0;
            }
        }
    }
}

.imgblock_middle {
    position: relative;
    .imgblock__visuel {
        min-height: 250px;
    }
    .imgblock__content {
        position: absolute;
        top: 50%;
        right: 0;
        left: 0;
        width: 90%;
        margin: 0 auto;
        transform: translateY(-50%);
    }
}

.imgblock__visuel {
    min-height: 350px;
    background-repeat: no-repeat;
    background-position: center center;
    background-size: cover;
    .imgblock_hp1 & {
        background-image: url("../img/heading/hp1-v2.jpg");
    }
    .imgblock_hp2 & {
        background-image: url("../img/heading/hp2-v2.jpg");
    }
    .imgblock_hp3 & {
        background-image: url("../img/heading/hp3-v2.jpg");
    }
    .imgblock_hp4 & {
        background-image: url("../img/heading/hp4-v2.jpg");
    }
    .imgblock_hp5 & {
        background-image: url("../img/heading/hp5-v2.jpg");
    }
    .imgblock_qsn1 & {
        background-image: url("../img/heading/qsn1.jpg");
    }
    .imgblock_qsn2 & {
        background-image: url("../img/heading/qsn2.jpg");
    }
    .imgblock_eq1 & {
        background-image: url("../img/heading/eq1.jpg");
    }
    .imgblock_cert1 & {
        background-image: url("../img/heading/cert1-v2.jpg");
    }
    .imgblock_cert2 & {
        background-image: url("../img/heading/cert2.jpg");
    }
    .imgblock_conf1 & {
        background-image: url("../img/heading/conf1.jpg");
    }
    .imgblock_real_professionnels & {
        background-image: url("../img/realisations/professionnels/habillage_mural_cp_banquette_s.jpg");
    }
    .imgblock_real_particuliers & {
        background-image: url("../img/realisations/particuliers/dressing_en_medium_peint.jpg");
    }
}



/* -------------------------------------------------------------------------*/
/* -- BLOC TXT */
/* -------------------------------------------------------------------------*/

.txtblock {
    padding: 1rem;
    background-color: $white;
    color: $bg-base;
    .txtblock__inner {
        border-color: $color-tertiary;
    }
}
.txtblock_titled {
    text-align: center;
    .txtblock__ttl {
        margin: 0;
        font-weight: normal;
        color: $color-tertiary;
        text-transform: uppercase;
        &:after {
            display: block;
            content: "";
            width: 60%;
            height: 2px;
            margin: 1.5rem auto .5rem auto;
            background-color: $color-primary;
        }
    }
}
.txtblock_bigger {
    .txtblock__txt {
        font-size: 2.2rem;
        font-style: italic;
    }
    .txtblock__btn {
        font-style: normal;
    }
}

.txtblock__inner {
    padding: 1rem;
    border-width: 1px;
    border-style: solid;
}

.txtblock__inner-link {
    transition: all .3s ease;
    color: $bg-base;
    &:focus,
    &:hover,
    &:active {
        background-color: $color-primary;
    }
}

.txtblock__topttl {
    font-weight: bold;
    font-size: 3rem;
}

.txtblock__ttl {
    margin-bottom: 1.5rem;
    font-size: 2.2rem;
    font-weight: bold;
    @include line-height(title);
    &:not(:first-child) {
        margin-top: 0;
    }

    .imgblock_real_particuliers & {
        text-align: center;
    }
    .imgblock_real_professionnels & {
        text-align: center;
    }
}

.txtblock__cat {
    font-size: 1.4rem;
    text-transform: uppercase;
    color: $color-tertiary;
}

.txtblock__img {
    margin-bottom: 1.5rem;
}

.txtblock__txt {
    hyphens: none;
}

.txtblock__btn {
    margin-top: 2rem;
}



/* -------------------------------------------------------------------------*/
/* -- PARTENAIRES */
/* -------------------------------------------------------------------------*/

.part {
    margin-top: 5rem;
    padding: 3rem;
    text-align: center;
}
.part_primary {
    background-color: $color-primary;
    color: $bg-base;
}

.part__inner {
    max-width: none;
    margin: 0 auto;
    display: flex;
    flex-wrap: wrap;
    margin-left: -1rem;
    margin-right: -1rem;
}

.part__ttl {
    margin-bottom: 2rem;
    font-size: 2.4rem;
}

.part__item {
    border-radius: 5px;
    background-color: $white;
    height: 90px;
    width: calc(100%/2 - 2rem);
    margin: 1rem;
    padding: 0 2rem;
    line-height: 85px;
    text-align: center;
    img {
        display: inline-block;
        max-height: 75%;
        max-width: 75%;
    }
}



/* -------------------------------------------------------------------------*/
/* -- EQUIPE */
/* -------------------------------------------------------------------------*/

.team {
    overflow: hidden;
}

.team__inner {
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    margin-left: -1rem;
    margin-right: -1rem;
}

.team__item {
    width: calc(100%/2 - 2rem);
    max-width: 200px;
    margin-left: 1rem;
    margin-right: 1rem;
    margin-bottom: 3rem;
}

.team__img {
    border-bottom: 5px solid $color-primary;
    margin-bottom: 1rem;
}

.team__name {
    font-weight: bold;
    hyphens: none;
}

.team__job {
    color: $color-primary;
    text-transform: uppercase;
    font-size: 1.4rem;
    hyphens: none;
}



/* -------------------------------------------------------------------------*/
/* -- REALISATIONS */
/* -------------------------------------------------------------------------*/

.rea__filters {
    display: none;
}

.rea__filter {
    display: block;
    padding: 1rem;
    font-size: 1.6rem;
    background-color: $color-tertiary;
    color: $white;
    text-transform: uppercase;
    text-align: center;
    transition: all .3s ease;
    margin-bottom: .2rem;
    &:focus,
    &:hover,
    &:active,
    &.is-active {
        background-color: $color-primary;
        color: $bg-base;
    }
}

.rea__items {
    margin-top: 2rem;
}

.rea__item {
    margin-bottom: 6rem;
    &.is-hidden {
        display: block;
    }
}

.msnry__container {
    margin-left: -0.5rem;
    margin-right: -0.5rem;
    overflow: hidden;
}
.msnry__item {
    width: calc(100% / 2 - 1rem);
    margin: 0.5rem;
}


/* -------------------------------------------------------------------------*/
/* -- GALERIE PHOTO */
/* -------------------------------------------------------------------------*/

.galerie {
    display: none;
}




/* -------------------------------------------------------------------------*/
/* -- FOOTER */
/* -------------------------------------------------------------------------*/

.footer__inner {
    max-width: $wrapsize;
    margin: 0 auto;
    padding: 3rem 0;
    text-align: center;
}

.footer__link {
    color: $white;
    & + .footer__link:before {
        content: "-";
        margin: 0 .5em;
    }
    &:focus,
    &:hover,
    &:active {
        color: $color-primary;
    }
}

.footer__nav {
    .footer__link {
        font-size: 1.4rem;
    }
}



/* -------------------------------------------------------------------------*/
/* -- COORDONNEES */
/* -------------------------------------------------------------------------*/

.coord__deco {
    display: none;
}

.coord__content {
    a:focus,
    a:hover,
    a:active {
        color: $white;
    }
}

.coord__name {
    font-size: 2.6rem;
    font-weight: bold;
    margin-bottom: 1rem;
}



/* -------------------------------------------------------------------------*/
/* -- MEDIA QUERY */
/* -------------------------------------------------------------------------*/

@media (min-width: $medium) {

    /* ------------------------------------*/
    /* -- [768] MAIN  */
    /* ------------------------------------*/

    .main__content {
        max-width: 80%;
        margin: 0 auto;
        font-size: 2.4rem;
    }

    .main__content_small {
        font-size: inherit;
    }


    /* ------------------------------------*/
    /* -- [768] LAYOUT  */
    /* ------------------------------------*/

    .layout_2 {
        .layout__col {
            width: calc(100%/2 - 3rem;)
        }
    }

    .layout__inner {
        display: flex;
        margin-left: -1.5rem;
        margin-right: -1.5rem;
    }

    .layout__col {
        margin-left: 1.5rem;
        margin-right: 1.5rem;
        & + .layout__col {
            margin-top: 0;
        }
    }


    /* ------------------------------------*/
    /* -- [768] BTN  */
    /* ------------------------------------*/

    .btn {
        display: inline-block;
    }

    .btn_giant {
        padding: 1.5rem 3rem;
    }


    /* ------------------------------------*/
    /* -- [768] HEADER */
    /* ------------------------------------*/

    .header__inner {
        display: flex;
        align-items: center;
        max-width: $wrapsize;
        margin: 1.5rem auto;
        padding-right: 1.5rem;
        padding-left: 1.5rem;
    }

    .header__item {
        flex-grow: 1;
        &:last-child {
            text-align: right;
        }
    }
    .header__item_small {
        max-width: 200px;
    }

    .header__logo {
        padding: 0;
    }


    /* ------------------------------------*/
    /* -- [768] NAV */
    /* ------------------------------------*/

    .nav__mobile {
        display: none;
    }

    .nav__inner {
        display: flex;
        justify-content: center;
        align-items: center;
    }

    .nav__item {
        font-size: 0;
        & + .nav__item {
            &:before {
                content: "▪";
                font-size: 1.8rem;
            }
        }
    }

    .nav__item_has-sub {
        position: relative;
        .nav__item:before {
            content: none;
        }
        &:hover {
            .nav__sub {
                opacity: 1;
                visibility: visible;
            }
        }
    }

    .nav__link {
        width: auto;
        border-top: 0;
        font-size: 1.4rem;
    }

    .nav__sub {
        position: absolute;
        z-index: 2;
        right: 0;
        left: .5rem;
        opacity: 0;
        visibility: hidden;
        transition: opacity .3s ease;
        .nav__link {
            display: block;
        }
    }

    .nav_primary {
        .nav__item:before {
            color: $bg-base;
        }
        .nav__sub {
            background-color: $color-tertiary;
            .nav__item {
                & + .nav__item {
                    border-top: 1px solid lighten($color-tertiary,7);
                }
            }
            .nav__link {
                transition: all .3s ease;
                color: $white;
                &:focus,
                &:hover,
                &:active {
                    background-color: darken($color-tertiary,7);
                }
            }
        }
    }

    /* ------------------------------------*/
    /* -- [768] ENCARD REALISATIONS  */
    /* ------------------------------------*/
    .encard {
        flex-direction: row;
        align-items: center;
        gap: unset;
        max-width: 90%;
        margin: 0 auto;

        .imgblock_duo {
            flex: 1 1 45%;
            max-width: 45%;
        }
    }


    /* ------------------------------------*/
    /* -- [768] BLOC TXT  */
    /* ------------------------------------*/

    .txtblock_bigger {
        .txtblock__txt {
            font-size: 2.4rem;
        }
    }

    .txtblock_titled {
        .txtblock__inner {
            padding: 5rem;
        }
    }

    .txtblock__ttl {
        font-size: 2.6rem;
    }
    .txtblock__btn {
        margin-top: 1rem;
    }

    .txtblock_photo {
        .txtblock__inner {
            display: flex;
        }
    }

    .txtblock__img {
        flex: 1 1 0%;
        margin-right: 2rem;
        margin-bottom: 0;
    }

    .txtblock__content {
        flex: 1 1 0%;
    }


    /* ------------------------------------*/
    /* -- [768] BLOC IMG  */
    /* ------------------------------------*/

    .imgblock_dual,
    .imgblock_dual-inv {
        flex-direction: row;
        .imgblock__visuel {
            flex: 1 0 0%;
            min-width: 40%;
            max-width: 40%;
            min-height: 0;
        }
        .imgblock__content {
            order: inherit;
            flex: 1 1 0%;
            width: auto;
            margin: 0;
        }
    }

    .imgblock_dual-inv {
        .imgblock__content {
            order: -1;
        }
    }

    .imgblock_middle {
        position: relative;
        .imgblock__visuel {
            min-height: 500px;
        }
        .imgblock__content {
            width: 50%;
        }
    }


    /* ------------------------------------*/
    /* -- [768] PARTENAIRES  */
    /* ------------------------------------*/

    .part__ttl {
        font-size: 2.8rem;
    }
    .part__item {
        width: calc(100%/3 - 2rem);
    }


    /* ------------------------------------*/
    /* -- [768] EQUIPE  */
    /* ------------------------------------*/

    .team__inner {
        margin-left: -1.5rem;
        margin-right: -1.5rem;
    }

    .team__item {
        width: calc(100%/2 - 3rem);
        margin-left: 1.5rem;
        margin-right: 1.5rem;
    }

    .team__name {
        font-size: 2rem;
    }


    /* ------------------------------------*/
    /* -- [768] REALISATIONS  */
    /* ------------------------------------*/

    .rea__filters {
        display: flex;
        flex-wrap: wrap;
        margin-left: -.15rem;
        margin-right: -.15rem;
    }

    .rea__filter {
        width: calc(100%/3 - .3rem);
        margin-left: .15rem;
        margin-right: .15rem;
    }

    .rea__item {
        &.is-hidden {
            display: none;
        }
    }
    
    .msnry__container {
        margin-left: -1rem;
        margin-right: -1rem;
    }
    .msnry__item {
        width: calc(100% / 3 - 2rem);
        margin: 1rem;
    }


    /* ------------------------------------*/
    /* -- [768] GALERIE PHOTO  */
    /* ------------------------------------*/

    .galerie {
        display: block;
        margin-top: 2rem;
    }

    .galerie__layout {
        display: flex;
        overflow: hidden;
        margin-left: -1rem;
        margin-right: -1rem;
        margin-bottom: -2rem;
    }

    .galerie__item {
        display: block;
        margin-bottom: 2rem;
    }

    .galerie__layout_grid {
        flex-wrap: wrap;
        .galerie__item {
            width: calc(100%/3 - 2rem);
            margin-left: 1rem;
            margin-right: 1rem;
        }
    }

    .galerie__col {
        width: calc(100%/3 - 2rem);
        margin-left: 1rem;
        margin-right: 1rem;
    }

    .galerie__col_double {
        width: calc(100%/3*2 - 2rem);
    }


    /* ------------------------------------*/
    /* -- [768] COORDONNEES  */
    /* ------------------------------------*/

    .coord {
        display: flex;
    }

    .coord__deco {
        display: block;
        margin-top: 2rem;
        margin-right: 3rem;
    }


}


@media (min-width: $large) {

    /* ------------------------------------*/
    /* -- [992] GLOBAL */
    /* ------------------------------------*/

    .main {
        padding-top: 8rem;
    }

    .main__section {
        & + .main__section {
            margin-top: 8rem;
        }
    }

    .main__content_small {
        .main__ttl {
            margin-bottom: 2rem;
        }
    }

    .main__ttl {
        margin-bottom: 4rem;
    }

    .sep {
        margin: 8rem 0;
        text-align: center;
        .footer & {
            margin: 5rem 0;
        }
    }


    /* ------------------------------------*/
    /* -- [992] FORMS */
    /* ------------------------------------*/

    .form__grp {
        display: flex;
        align-items: center;
    }
    .form__grp_right {
        justify-content: flex-end;
    }
    .form__grp_labeltop {
        align-items: flex-start;
    }

    .form__label,
    .form__input {
        flex: 1 1 0%;
    }

    .form__label {
        max-width: 100px;
    }


    /* ------------------------------------*/
    /* -- [992] BTN */
    /* ------------------------------------*/

    .btn_giant {
        padding: 2rem 10rem;
    }


    /* ------------------------------------*/
    /* -- [992] HEADER */
    /* ------------------------------------*/

    .header__inner {
        margin-top: 3rem;
        margin-bottom: 3rem;
    }

    .header__logo {
        max-width: none;
    }


    /* ------------------------------------*/
    /* -- [992] NAV */
    /* ------------------------------------*/

    .nav__item {
        & + .nav__item {
            &:before {
                margin: 0 .75rem;
            }
        }
    }

    .nav__link {
        padding-top: 2rem;
        padding-bottom: 1.5rem;
        font-size: 1.55rem;
        &[class*="icon"] {
            font-size: 2rem;
        }
        &:after {
            display: block;
            content: "";
            width: 0%;
            height: 2px;
            margin: .5rem auto 0 auto;
            transition: width .3s ease;
        }
        &:focus,
        &:hover,
        &:active,
        &.nav__link_current {
            &:after {
                width: 60%;
            }
        }
    }

    .nav__sub {
        left: 2rem;
        .nav__link {
            padding: 1rem;
            font-size: 1.4rem;
            line-height: 1.2;
            &:after {
                content: none;
            }
        }
    }

    .nav_primary {
        .nav__link {
            &:after {
                background-color: $color-tertiary;
            }
        }
        .nav__item_has-sub {
            &:focus,
            &:hover,
            &:active {
                & > .nav__link:after {
                    width: 60%;
                }
            }
        }
    }


    /* ------------------------------------*/
    /* -- [992] BLOC TXT  */
    /* ------------------------------------*/

    .txtblock {
        position: relative;
        padding: 1rem;
    }

    .txtblock__inner {
        padding: 1.5rem;
    }

    .txtblock__topttl {
        position: absolute;
        top: -5rem;
        right: 0;
        color: $white;
    }

    .txtblock__img {
        margin-right: 4rem;
    }



    /* ------------------------------------*/
    /* -- [992] BLOC IMG  */
    /* ------------------------------------*/

    .imgblock_dual,
    .imgblock_dual-inv {
        align-items: center;
        .imgblock__visuel {
            min-width: 650px;
            max-width: 650px;
            height: 440px;
        }
        .imgblock__content {
            margin-left: -20%;
        }
    }

    .imgblock_dual_top {
        align-items: flex-start;
        .imgblock__content {
            margin-top: 2%;
        }
    }

    .imgblock_dual-inv {
        .imgblock__content {
            margin-right: -20%;
            margin-left: 0;
        }
    }

    .imgblock_middle {
        .imgblock__content {
            width: 40%;
        }
    }
    .imgblock_real_particuliers .imgblock__content, .imgblock_real_professionnels .imgblock__content {
        width: 50%;
    }


    /* ------------------------------------*/
    /* -- [992] PARTENAIRES  */
    /* ------------------------------------*/

    .part__ttl {
        margin-bottom: 3rem;
        font-size: 3.6rem;
    }
    .part__item {
        width: calc(100%/5 - 2rem);
    }


    /* ------------------------------------*/
    /* -- [992] REALISATIONS  */
    /* ------------------------------------*/

    .rea__filters {
        flex-wrap: nowrap;
        margin-left: 0;
        margin-right: 0;
    }

    .rea__filter {
        width: auto;
        flex-grow: 1;
        margin: 0;
        & + .rea__filter {
            margin-left: .2rem;
        }
    }


}


@media (min-width: $wrapsize + 30px) {

    /* ------------------------------------*/
    /* -- [WRAP] HEADER */
    /* ------------------------------------*/

    .header__inner {
        padding-right: 0;
        padding-left: 0;
    }

}
