@import "reboot";
@import "config";

// -------------------------------------------------------------------------
// -- BASIC STYLES
// -------------------------------------------------------------------------

html {
    box-sizing: border-box;
}
*,
*::before,
*::after {
    min-width: 0;
    min-height: 0;
    box-sizing: inherit;
}

html {
    font-size: 62.5%;
    font-size: calc(1em * .625);
}

body {
    margin: 0;
    color: $color-base;
    background-color: $bg-base;
    font-family: $font-family-base;
    @include font-size(base);
    @include line-height(base);
}

a {
    color: $link-color;
    text-decoration: none;
    transition: color .3s ease;
    &:focus,
    &:hover,
    &:active {
        color: $link-color-hover;
        text-decoration: none;
    }
}

h1,
.h1-like {
    @include font-size(h1);
    @include line-height(title);
    @if variable_exists(font-family-headings) and $font-family-headings != $font-family-base {
        font-family: $font-family-headings;
    }
}
h2,
.h2-like {
    @include font-size(h2);
    @include line-height(title);
    @if variable_exists(font-family-headings) and $font-family-headings != $font-family-base {
        font-family: $font-family-headings;
    }
}
h3,
.h3-like {
    @include font-size(h3);
    @include line-height(title);
    @if variable_exists(font-family-headings) and $font-family-headings != $font-family-base {
        font-family: $font-family-headings;
    }
}
h4,
.h4-like {
    @include font-size(h4);
    @include line-height(title);
    @if variable_exists(font-family-headings) and $font-family-headings != $font-family-base {
        font-family: $font-family-headings;
    }
}
h5,
.h5-like {
    @include font-size(h5);
    @include line-height(title);
    @if variable_exists(font-family-headings) and $font-family-headings != $font-family-base {
        font-family: $font-family-headings;
    }
}
h6,
.h6-like {
    @include font-size(h6);
    @include line-height(title);
    @if variable_exists(font-family-headings) and $font-family-headings != $font-family-base {
        font-family: $font-family-headings;
    }
}

h1,
h2,
h3,
h4,
h5,
h6,
dd {
    font-weight: bold;
    margin-top: 0;
    margin-bottom: $margin-bottom-headings;
    &:not(:first-child) {
        margin-top: $margin-top-headings;
    }
}

p,
address,
ol,
ul,
dl,
blockquote,
pre {
    margin-top: 0;
    margin-bottom: $margin-bottom-paragraph;
}

li p,
li .p-like,
li ul,
li ol,
ol ol,
ul ul {
    margin-top: 0;
    margin-bottom: 0;
}

img,
table,
td,
blockquote,
code,
pre,
textarea,
input,
video,
svg {
    max-width: 100%;
}

img {
    height: auto;
}

ul,
ol {
    padding-left: 2em;
}

img {
    vertical-align: middle;
}

em,
.italic,
address,
cite,
i,
var {
    font-style: italic;
}

sup,
sub {
    vertical-align: 0;
}

sup {
    bottom: 1ex;
}

sub {
    top: .5ex;
}

hr {
    display: block;
    height: 1px;
    clear: both;
    margin: 1em 0 2em;
    padding: 0;
    color: $gray-200;
    border: 0;
    background-color: $gray-200;
}

@media (max-width: ($small - 1)) {
    div,
    textarea,
    table,
    td,
    th,
    code,
    pre,
    samp {
        word-wrap: break-word;
        hyphens: auto;
    }
}

@media screen and (-ms-high-contrast: active), (-ms-high-contrast: none) {
    img[src$=".svg"] {
        width: 100%;
    }
}



// -------------------------------------------------------------------------
// -- LAYOUT / HELPERS
// -------------------------------------------------------------------------

.container {
    max-width: $wrapsize;
    margin-right: auto;
    margin-left: auto;
    padding-right: 1.5rem;
    padding-left: 1.5rem;
    @media (min-width: $wrapsize + 30px) {
        padding-right: 0;
        padding-left: 0;
    }
}

.bfc {
    overflow: hidden;
}

.clear {
    clear: both;
}
.clearfix {
    &::after {
        display: table;
        clear: both;
        content: "";
        border-collapse: collapse;
    }
}

.left {
    margin-right: auto;
}

.right {
    margin-left: auto;
}

.center {
    margin-right: auto;
    margin-left: auto;
}

.txtleft {
    text-align: left;
}

.txtright {
    text-align: right;
}

.txtcenter {
    text-align: center;
}

.txtjustify {
    text-align: justify;
}

.fleft {
    float: left;
}

img.fleft {
    margin-right: 1em;
}

.fright {
    float: right;
}

img.fright {
    margin-left: 1em;
}

img.fleft,
img.fright {
    margin-bottom: .75em;
}



// -------------------------------------------------------------------------
// -- UTILITIES
// -------------------------------------------------------------------------

.bold {
    font-weight: bolder;
}

.normal {
    font-weight: normal;
    font-style: normal;
}

.uppercase {
    text-transform: uppercase;
}

.lowercase {
    text-transform: lowercase;
}

.smaller {
    font-size: .6em;
}

.small {
    font-size: .8em;
}

.big {
    font-size: 1.2em;
}

.bigger {
    font-size: 1.5em;
}

.biggest {
    font-size: 2em;
}

.is-hidden,
[hidden] {
    display: none;
}

.visually-hidden {
    position: absolute !important;
    width: 1px !important;
    height: 1px !important;
    overflow: hidden !important;
    clip: rect(0, 0, 0, 0) !important;
    padding: 0 !important;
    border: 0 !important;
}

.skip-links {
    position: absolute;
    & a {
        position: absolute;
        overflow: hidden;
        clip: rect(1px, 1px, 1px, 1px);
        padding: .5em;
        color: white;
        background: black;
        text-decoration: none;
        &:focus {
            position: static;
            overflow: visible;
            clip: auto;
        }
    }
}

.is-disabled,
[disabled],
.is-disabled ~ label,
[disabled] ~ label {
    cursor: not-allowed !important;
    opacity: .5;
    filter: grayscale(1);
}

ul {
    &.is-unstyled,
    &.unstyled {
        padding-left: 0;
        list-style: none;
    }
}



// -------------------------------------------------------------------------
// -- RESPONSIVE
// -------------------------------------------------------------------------

@media (min-width: $large) {

    .large-hidden {
        display: none !important;
    }

    .large-visible {
        display: block !important;
    }

}

@media (min-width: $medium)  {

    .medium-hidden {
        display: none !important;
    }

    .medium-visible {
        display: block !important;
    }

}

@media (min-width: $small) {

    .small-hidden {
        display: none !important;
    }

    .small-visible {
        display: block !important;
    }

}

@media (min-width: $tiny) {

    .tiny-hidden {
        display: none !important;
    }

    .tiny-visible {
        display: block !important;
    }

}



// -------------------------------------------------------------------------
// -- FORMS
// -------------------------------------------------------------------------

.btn {
    display: inline-block;
}

[type="color"],
[type="date"],
[type="datetime-local"],
[type="email"],
[type="month"],
[type="number"],
[type="password"],
[type="search"],
[type="submit"],
[type="tel"],
[type="text"],
[type="time"],
[type="url"],
[type="week"],
select,
textarea {
  white-space: nowrap;
  border: 0;
  color: $color-base;
  vertical-align: middle;
  padding: .5rem 1rem;
  margin: 0;
}

[type="submit"] {
  cursor: pointer;
}

label {
    cursor: pointer;
    vertical-align: middle;
}

textarea {
  min-height: 5em;
  vertical-align: top;
  white-space: normal;
}

input[type="search"] {
    &::-webkit-search-decoration,
    &::-webkit-search-cancel-button,
    &::-webkit-search-results-button,
    &::-webkit-search-results-decoration {
        display: none;
    }
}

::-webkit-input-placeholder {
    color: $gray-500;
}

input:-moz-placeholder,
textarea:-moz-placeholder {
    color: $gray-500;
}



// -------------------------------------------------------------------------
// IMPORTS
// -------------------------------------------------------------------------

@import "custom";
